import React, { useEffect, useState } from "react";
import { Pc, Mobile } from "../components/reponsiveCheck";
import { ReactComponent as LeftArrow } from "../assets/chevron_left.svg";
import { ReactComponent as RightArrow } from "../assets/chevron_right.svg";
import { useNavigate } from "react-router-dom";

const SurveyPage = () => {
  const questions = [
    {
      index: 0,
      question: "  ?",
      option: [
        { index: 0, number: "A", data: "" },
        { index: 1, number: "B", data: "" },
      ],
    },
    {
      index: 1,
      question: "   ?",
      option: [
        { index: 0, number: "A", data: "10" },
        { index: 1, number: "B", data: "20" },
        { index: 2, number: "C", data: "30" },
        { index: 3, number: "D", data: "40 " },
      ],
    },
    {
      index: 2,
      question: "   ?",
      option: [
        { index: 0, number: "A", data: "" },
        { index: 1, number: "B", data: "" },
      ],
    },
    {
      index: 3,
      question: "    ?",
      option: [
        { index: 0, number: "A", data: "" },
        { index: 1, number: "B", data: "SF" },
        { index: 2, number: "C", data: "//" },
        { index: 3, number: "D", data: "" },
        { index: 4, number: "E", data: "" },
      ],
    },
    {
      index: 4,
      question: "    ?",
      option: [
        { index: 0, number: "A", data: "" },
        { index: 1, number: "B", data: "" },
        { index: 2, number: "C", data: "" },
        { index: 3, number: "D", data: "" },
        { index: 4, number: "E", data: "" },
      ],
    },
    {
      index: 5,
      question: "   !",
      option: [
        {
          index: 0,
          number: "A",
          data: "  ",
          isChecked: false,
        },
        { index: 1, number: "B", data: " " },
        { index: 2, number: "C", data: " X " },
        {
          index: 3,
          number: "D",
          data: "  ",
          isChecked: false,
        },
        { index: 4, number: "E", data: " " },
        { index: 5, number: "F", data: " " },
        { index: 6, number: "G", data: "" },
      ],
    },
  ];

  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [selectedOptions, setSelectedOptions] = useState([]);
  const [userSelection, setUserSelection] = useState([]);
  const [progress, setProgress] = useState(0);
  const [progressWidth, setProgressWidth] = useState(0);

  const navigation = useNavigate();

  useEffect(() => {
    requestAnimationFrame(() => {
      setProgressWidth(progress);
    });
  }, [progress]);

  const ProgressBar = () => {
    return (
      <div className="progressBarWrapper">
        <div className="progressBar">
          <div
            className="progressBar-fill"
            style={{
              width: `${progressWidth}%`,
            }}
          >
            {" "}
          </div>
        </div>
      </div>
    );
  };

  const handleOptionClick = (optionIndex) => {
    setSelectedOptions((prevSelectedOptions) => {
      if (prevSelectedOptions.includes(optionIndex)) {
        return prevSelectedOptions.filter((index) => index !== optionIndex);
      } else {
        return [...prevSelectedOptions, optionIndex];
      }
    });
  };

  const handleSignUpDone = () => {
    alert(" !  .");
    navigation("/intro");
  };

  const nextQuestion = () => {
    const selectedData = selectedOptions.map(
      (optionIndex) =>
        questions[currentQuestionIndex].option.find(
          (option) => option.index === optionIndex
        ).data
    );
    setUserSelection([...userSelection, selectedData]);
    if (currentQuestionIndex < questions.length - 1) {
      setCurrentQuestionIndex(currentQuestionIndex + 1);
      setSelectedOptions([]);
      setProgress(progress + 17);
    }
    if (currentQuestionIndex === questions.length - 1) {
      setProgress(100);
      handleSignUpDone();
    }
  };

  const prevQuestion = () => {
    if (currentQuestionIndex > 0) {
      setCurrentQuestionIndex(currentQuestionIndex - 1);
      setSelectedOptions([]);
      setProgress(progress - 17);
    }
  };

  return (
    <>
      <Pc>
        <div className="surveyWrapper">
          <div>
            <div className="surveyContentsWrapper">
              <ProgressBar />
              <div className="surveyContents-wrapped">
                <div className="question">
                  <h2>{questions[currentQuestionIndex].question}</h2>
                </div>
                <div className="surveyOptionsWrapper">
                  {questions[currentQuestionIndex].option.map(
                    (option, index) => (
                      <div
                        key={index}
                        className={
                          selectedOptions.includes(option.index)
                            ? "surveyOptionSelected"
                            : "surveyOption"
                        }
                      >
                        <button
                          key={option.index}
                          onClick={() => handleOptionClick(option.index)}
                        >
                          {option.number}
                        </button>
                        <p>{option.data}</p>
                      </div>
                    )
                  )}
                </div>
                <div className="buttonsWrapper">
                  <div className="prevButton">
                    <button
                      onClick={prevQuestion}
                      disabled={currentQuestionIndex === 0}
                    >
                      <LeftArrow />
                    </button>
                  </div>
                  <div className="nextButton">
                    <button onClick={nextQuestion}>
                      <RightArrow />
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Pc>
      <Mobile>
        <div className="surveyContentsWrapper">
          <ProgressBar />
          <div className="surveyContents-wrapped">
            <div className="question">
              <h2>{questions[currentQuestionIndex].question}</h2>
            </div>
            <div className="surveyOptionsWrapper">
              {questions[currentQuestionIndex].option.map((option, index) => (
                <div
                  key={index}
                  className={
                    selectedOptions.includes(option.index)
                      ? "surveyOptionSelected"
                      : "surveyOption"
                  }
                >
                  <button
                    key={option.index}
                    onClick={() => handleOptionClick(option.index)}
                  >
                    {option.number}
                  </button>
                  <p>{option.data}</p>
                </div>
              ))}
            </div>
            <div className="buttonsWrapper">
              <div className="prevButton">
                <button
                  onClick={prevQuestion}
                  disabled={currentQuestionIndex === 0}
                >
                  <LeftArrow />
                </button>
              </div>
              <div className="nextButton">
                <button onClick={nextQuestion}>
                  <RightArrow />
                </button>
              </div>
            </div>
          </div>
        </div>
      </Mobile>
    </>
  );
};

export default SurveyPage;
