import React, { useState } from "react";
import "../styles/App.css";
import { ReactComponent as FirstIll } from "../images/intro/firstIll.svg";
import { ReactComponent as SecondIll } from "../images/intro/secondIll.svg";
import { Mobile, Pc } from "../components/reponsiveCheck";
import { AuthModal } from "../components/authModal";
import { useNavigate } from "react-router-dom";
import { isLogin } from "../utils/auth";
import Pic1 from "../images/intro/pic1.png";
import Pic2 from "../images/intro/pic2.png";
import useIntersectionObserver from "../utils/useIntersectionObserver";

const IntroPage = () => {
  const [isClicked, setIsClicked] = useState(false);
  const isLoggedin = isLogin();
  const navigation = useNavigate();
  const { addRef, isInViewports } = useIntersectionObserver();

  const controlModal = () => {
    if (isLoggedin) {
      navigation("/home");
    } else {
      setIsClicked(!isClicked);
    }
  };

  const onClickSignUp = () => {
    if (isLoggedin) {
      navigation("/home");
    } else {
      navigation("/surveyintro");
    }
  };

  return (
    <>
      <Pc>
        <div className="introWrapper">
          <header className="header">
            <div className="signInButton">
              <button onClick={controlModal}>SIGN IN</button>
            </div>
          </header>
          <div className="sections">
            <section className="firstSection">
              <div className="textArea">
                <h1></h1>
                <p id="title">       </p>
                <p id="content">
                         
                   AI  .
                  <br />
                     PC     
                  .
                </p>
              </div>
              <div className="firstImage">
                <img
                  src={Pic1}
                  alt="first"
                  style={{ width: 300, height: "auto" }}
                />
              </div>
            </section>
            <div className="firstIll">
              <div className="svgWrapper">
                <FirstIll />
              </div>

              <div className="textArea">
                <p id="title-2">
                      AI  
                  <br />    
                </p>
                <p id="content-2">
                       ,
                  <br />
                         .
                  <br />
                     ,    .
                </p>
              </div>
            </div>
            <div className="secondWrapper">
              <div className="imageWrapper-2">
                <div className="secondImagePhone">
                  <img
                    src={Pic2}
                    alt="first"
                    style={{ width: 300, height: "auto" }}
                  />
                </div>
                <div className="secondTextWrapper">
                  <p id="title">    </p>
                  <p id="content">
                            .
                    <br />
                         ,
                    <br />
                          ! 
                  </p>
                </div>
              </div>
              <div className="secondImage">
                <SecondIll />
              </div>
            </div>
            <section className="lastSection">
              <button onClick={onClickSignUp}>  </button>
            </section>
          </div>
        </div>
      </Pc>
      <Mobile>
        <div className="introWrapper">
          <header className="header">
            <div className="signInButton">
              <button onClick={controlModal}>SIGN IN</button>
            </div>
          </header>
          <div className="sections">
            <section className="firstSection-mobile">
              <div className="textArea-mobile">
                <h1></h1>
                <p id="title-mobile">
                         
                </p>
                <p id="content-mobile">
                         AI  
                </p>
              </div>
              <div
                ref={addRef}
                className={isInViewports[0] ? "imageWrapper-mobile-1" : ""}
              >
                <img src={Pic1} alt="first" className="image-mobile" />
              </div>
            </section>
            <section className="secondSection-mobile">
              <div className="textArea-mobile">
                <p
                  ref={addRef}
                  className={isInViewports[1] ? "title-2-mobile" : ""}
                >
                      AI  
                  <br />    
                </p>
                <p
                  ref={addRef}
                  className={isInViewports[2] ? "content-2-mobile" : ""}
                >
                       ,
                  <br />
                         .
                  <br />
                     ,    .
                </p>
              </div>
            </section>
            <div className="secondWrapper-mobile">
              <div
                ref={addRef}
                className={isInViewports[3] ? "imageWrapper-mobile-2" : ""}
              >
                <img src={Pic2} alt="first" className="image-mobile" />
              </div>
              <div className="textArea-mobile">
                <p
                  ref={addRef}
                  className={isInViewports[4] ? "title-3-mobile" : ""}
                >
                      
                </p>
                <p
                  ref={addRef}
                  className={isInViewports[5] ? "title-3-content" : ""}
                >
                          .
                  <br />
                       ,
                  <br />
                        ! 
                </p>
              </div>
            </div>
            <section className="lastSection-mobile">
              <button onClick={onClickSignUp}>  </button>
            </section>
          </div>
        </div>
      </Mobile>
      {isClicked && <AuthModal isClosed={(e) => setIsClicked(!isClicked)} />}
    </>
  );
};

export default IntroPage;
